___CSS_LOADER_EXPORT___.push([module.id, `/* Global Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
    font-family: Arial, sans-serif;
    background: #000;
    overflow: hidden;
  }
  
  /* Canvas Styling */
  canvas {
    display: block;
    background: black;
  }
  
  /* Menu Overlay Styling */
  #menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.8);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
  }
  
  #header {
    width: 100%;
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  #header h1 {
    font-size: 5em;
    margin: 0;
  }
  
  #instructions {
    width: 80%;
    text-align: center;
    margin: 20px 0;
    font-size: 1.2em;
    line-height: 1.5;
  }
  
  #startButton {
    font-size: 2em;
    padding: 10px 20px;
    cursor: pointer;
    border: none;
    background: #333;
    color: white;
    border-radius: 5px;
    transition: background 0.3s ease;
  }
  
  #startButton:hover {
    background: #555;
  }
  
  /* Game Over Menu Styling */
  #gameOverMenu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.8);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: -100;
  }
  
  #gameOverMenu h1 {
    font-size: 4em;
    margin-bottom: 20px;
  }
  
  #restartButton {
    font-size: 1.8em;
    padding: 10px 20px;
    cursor: pointer;
    border: none;
    background: #333;
    color: white;
    border-radius: 5px;
    transition: background 0.3s ease;
  }
  
  #restartButton:hover {
    background: #555;
  }
